import { useState, useEffect, useRef } from 'react';
import SideBar from '../components/Admin/Sidebar';
import { Toast } from 'primereact/toast';

const Settings = () => {

    const toast = useRef(null);

    const curPassword = useRef(null)

    const [password, $password] = useState('...');

    // Get universal password
    useEffect(() => {
        async function getData() {
            const request = await fetch('/api/find/password', {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            })
    
            const response = await request.json()
            if (response.ok) {
                $password(response.data);
            } else {
                console.log(`    : ${response.message}`);
            }
        }
        getData();
        
    }, [])

    async function setNewPassword() {

        const params = {
            name: 'universalPassword',
            data: curPassword.current.value
        }
        
        const request = await fetch('/api/setting/password', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(params)
        });

        const response = await request.json();
        if (response.ok) {
            $password(curPassword.current.value);
        } else {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: response.message, life: 5000});
        }

        curPassword.current.value = '';
    }

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_block admin_content_block--max">
                    <div className="admin_content_block_header">
                        <h3>Universal password for bots</h3>
                    </div>
                    <div className="admin_content_block_body scrollStyle">
                        <div className="row">
                            <div className="column">Current password: <span>{ password }</span></div>
                        </div>
                        <div className="row">
                            <div className="column"><input ref={ curPassword } type="text" id="password" placeholder="My answer" /></div>
                        </div>
                        <div className="row">
                            <div className="column"><button className="mini-button" onClick={ setNewPassword }>Save</button></div>
                        </div>
                    </div>
                </div>
            </div>
            <Toast ref={ toast } />
        </div>
    )
}

export default Settings