import { useState, useEffect, useRef } from 'react';
import CloseImg from '../../assets/close.png';
import { Toast } from 'primereact/toast';

const MessageModal = data => {

    const toast = useRef(null);

    const bot_name = useRef(null);
    const day_week = useRef(null);
    const textMessage = useRef(null);

    const [botsName, $botsName] = useState([]);

    useEffect(() => {
        async function getData() {
            await getUsers();
        }
        getData();

    }, [])

    if (!data.show) return null;

    async function saveNewMessage(e) {
        const params = {
            text: textMessage.current.value, 
            day: day_week.current.value, 
            sender: bot_name.current.value
        }

        if (!params.text || !params.day || !params.sender) {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: 'Not all fields are filled', life: 5000});
            return;
        }

        const responce = await fetch('/api/message/create', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(params)
        });
      
        const candidate = await responce.json();
        if (candidate.ok) {
            data.closeWindow();
            data.updateTable();
        } else {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: candidate.message, life: 5000});
        }
    }

    async function getUsers() {
        const request = await fetch('/api/find/users', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ returnArray: true })
        })
    
        const response = await request.json();
        
        if (response.ok) {
            const users = response.users;
            $botsName(users);
        } else {
            console.log(response.message);
        }
    }

    return (
        <div className="modal_back">
            <div className="modal_window">
                <h3>Create new Message</h3>
                <div className='closeModal' onClick={ data.closeWindow }><img src={CloseImg} alt="" /></div>
                <div className="row">
                    <div className="col">
                        <p>Bot name</p>
                        <select ref={ bot_name } id="bot_name" className='width--max'>
                            { botsName.map(name => (
                                <option value={name} key={name}>{ name }</option>
                            )) }
                        </select>
                    </div>
                    <div className="col">
                        <p>Day Week</p>
                        <input ref={ day_week } type="text" id='day_week' className='width--max' value={ data.dayWeek } disabled />
                    </div>
                </div>
                <div className="row">
                    <div className="col">
                        <p>Text message</p>
                        <textarea ref={ textMessage } id="message" placeholder='My answer'></textarea>
                    </div>
                </div>

                <div className="row">
                    <div className="col flex-row-center m-t-20">
                        <button className='mini-button back-green' onClick={ saveNewMessage }>Save</button>
                    </div>
                </div>
            </div>
            <Toast ref={ toast } />
        </div>
    )
}

export default MessageModal