import React from "react"

const Table = data => {
  const dataTable = data.data

  return (
    <table>
      <thead>
        <tr>
          {
            dataTable.head.map((el, index) => (
              <th key={'thead' + index} scope="col">{el}</th>
            ))
          }
        </tr>
      </thead>
      <tbody>
        {
          dataTable.body.map((element, index) => (
            <tr key={'tbody' + index}>
              {
                element.map((el, ind) => (
                  <td key={ind+'_'+index}>{el}</td>
                ))
              }
            </tr>
          ))
        }

      </tbody>
    </table>
  )
}



export default Table