import { useState, useEffect, useRef } from 'react';
import CloseImg from '../../assets/close.png'
import { Toast } from 'primereact/toast';

const cartData = {
    Mon: [],
    Tue: [],
    Wed: [],
    Thu: [],
    Fri: [],
    Sat: [],
    Sun: []
}

const BotModal = data => {

    const toast = useRef(null);
    const login = useRef(null);
    const password = useRef(null);
    const currency = useRef(null);
    const min_amount = useRef(null);
    const max_amount = useRef(null);
    const lucky = useRef(null);

    const [daysData, $daysData] = useState(cartData);
    const [defaultPassword, $defaultPassword] = useState('');

    useEffect(() => {
        async function getData() {
            const request = await fetch('/api/find/password', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
            })
        
            const response = await request.json()
            if (response.ok) {
            $defaultPassword(response.data);
            } else {
            console.log(`    : ${response.message}`);
            }
        }
        getData();
    
    }, [])

    if (!data.show) return null;

    function toggleSetInterval(e) {
        const main = e.target.closest('.iterval_block');
        main.querySelector('.inputs-interval').classList.toggle('--hidden');
        main.querySelector('.actions-interval').classList.toggle('--hidden');
        main.querySelector('.add-interval').classList.toggle('--hidden');
    }
    function addInterval(e) {
        const main = e.target.closest('.iterval_block');
        const day = main.id;
        const start = main.querySelector('.start').value;
        const end = main.querySelector('.end').value;

        if (!start || !end) {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: 'No time specified', life: 5000});
            return
        } 

        if (Number(start.split(':')[0]) > Number(end.split(':')[0])) {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: 'The beginning cannot be greater than the end', life: 5000});
            return;
        }

        if (day in daysData) {
            let isPeriod = daysData[day].find(row => row.start === start || row.end === end);
            if (isPeriod) {
                toast.current.show({ severity: 'error', summary: 'Warning Message', detail: 'Period already exists', life: 5000});
                return;
            }


            daysData[day].push({ start, end });
            $daysData({...daysData});
        }

    }

    async function saveNewBot(e) {
        const params = {
            login: login.current.value,
            password: password.current.value,
            currency: currency.current.value,
            amountMin: min_amount.current.value,
            amountMax: max_amount.current.value,
            luck: lucky.current.value,
            intevals: daysData,
            typeUser: 'Bot',
        }

        const keys = Object.keys(daysData);
        let status = false;
        for (let row of keys) {
            if (daysData[row].length > 0) {
                status = true;
                break;
            }
        }

        if (!params.login || !params.password || !params.amountMin || !params.amountMax || !params.luck || !status) {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: 'Not all fields are filled', life: 5000});
            return;
        }

        const responce = await fetch('/api/auth/register', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(params)
        });
      
        const candidate = await responce.json();
        if (candidate.ok) {
            data.closeWindow();
            data.updateTable();
        } else {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: candidate.message, life: 5000});
        }

    }

    return (
        <div className="modal_back">
            <div className="modal_window">
                <h3>Create new Bot</h3>
                <div className='closeModal' onClick={ data.closeWindow }><img src={CloseImg} alt="" /></div>
                <div className="row">
                    <div className="col">
                        <p>Login</p>
                        <input ref={ login } type="text" className='width--max' id='login' placeholder='My answer' />
                    </div>
                    <div className="col">
                        <p>Password</p>
                        <input ref={ password } type="text" className='width--max' value={ defaultPassword } id='password' placeholder='My answer' readOnly />
                    </div>
                </div>

                <div className="row">
                    <div className="col">
                        <p>Currency</p>
                        <select ref={ currency } id="currency" className='width--max'>
                            <option value="BTC">BTC</option>
                            <option value="ETH">ETH</option>
                        </select>
                    </div>
                    <div className="col">
                        <p>Bet amount</p>
                        <div className="row">
                            <div className="col">
                                <input ref={ min_amount } type="text" className='width--max' id='min' placeholder='Min value' />
                            </div>
                            <div className="col">
                                <input ref={ max_amount } type="text" className='width--max' id='max' placeholder='Max value' />
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col">
                        <div className="double_title">
                            <p>Bot luck</p>
                            <p>50%</p>
                        </div>
                        <input ref={ lucky } type="range" className="width--max" />
                    </div>
                </div>

                <div className="row">
                { Object.keys(daysData).map(day => (
                    <div className="col col-m-r-5" key={ Math.random().toString(36).substring(3) }>
                        <div className="iterval_block" id={ day }>
                            <div className="interval_block_head">{ day }</div>
                            <div className="interval_block_body">
                                { daysData[day].map(row => (
                                    <div className="double_title" key={ Math.random().toString(36).substring(3) }>
                                        <p>{ row.start }-{ row.end }</p>
                                        <img src={ CloseImg } alt="" />
                                    </div>
                                )) }

                                <div className="row --hidden inputs-interval">
                                    <div className="col">
                                        <input type="time" className='start' />
                                        <input type="time" className='end' />
                                    </div>
                                </div>
                            </div>
                            <div className="interval_block_footer">
                                <div className="row add-interval">
                                    <div className="col flex-row-center"><button className="mini-button" onClick={ toggleSetInterval }>Add interval</button></div>
                                </div>
                                <div className="row --hidden actions-interval">
                                    <div className="col col-m-r-5"><button className="mini-button back-red" onClick={ toggleSetInterval }>Cancel</button></div>
                                    <div className="col col-m-r-5"><button className="mini-button back-green" onClick={ addInterval }>Save</button></div>
                                </div>
                            </div>
                        </div>
                    </div>
                )) }
                </div>

                <div className="row">
                    <div className="col flex-row-center m-t-20">
                        <button className='mini-button back-green' onClick={ saveNewBot }>Save</button>
                    </div>
                </div>
            </div>
            <Toast ref={ toast } />
        </div>
    )
}

export default BotModal