import React, { useState, useEffect } from "react";
import Chat from '../components/Site/Chat';

import io from 'socket.io-client'
const socket = io();

const Home = () => {

  const [messages, $messages] = useState([]);

  useEffect(() => {

    socket.on('connect', () => {
      console.log('connecting ...');
    });

    socket.on('getMessages', (socket) => {
      $messages(socket);
    })
  
    socket.on('connectionData', (data) => {
      const { messages, users } = data;
  
      $messages(messages);
      //setPlayers(users);
    })
  
    // GAMESTEP
    /* socket.on('gameStep', data => {
      setGrafData(data);
    }); */

  })

  const sendMessage = (message) => {

    console.log('Send message ...');

    //   ,   
    if (message.text) {
      console.log('Text-is');
      socket.emit('sendMessage', message)
    }

  }

  return (
    <div className="main">
      <div className="header">
        <div className="header__item fs-22">BE</div>
        <div className="header__item">
          <p>ETH: <span>0.00</span></p>
          <p>BTC: <span>0.00</span></p>
        </div>
        <div className="header__item">
          <a href="/login">Login</a>
          <a href="/register">Register</a>
        </div>
      </div>
      <div className="content">
        <div className="content_left">
          <div className="block_game">
            <div className="block_game__game">
              <div className="game__graph"></div>
              <div className="game__results">
                <div className="item_result color-red">3.24x</div>
                <div className="item_result color-green">3.24x</div>
                <div className="item_result color-red">3.24x</div>
                <div className="item_result color-green">3.24x</div>
                <div className="item_result color-green">3.24x</div>
              </div>
            </div>
            <div className="block_game__bet">
              <div className="game_bet__row">
                <button>Login to play</button>
              </div>
              <div className="game_bet__row">
                or <a href="/register">register</a>
              </div>
            </div>
          </div>
          <Chat onSendMessage={ sendMessage } messages={ messages } />
        </div>
        <div className="content_right">
          <div className="block_players">
            <div className="players_body scrollStyle">
              <table>
                <thead>
                  <tr>
                    <th>User</th>
                    <th>@</th>
                    <th>Bet</th>
                    <th>Profit</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                  </tr>
                  <tr>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                  </tr>
                  <tr>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                  </tr>
                  <tr>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                  </tr>
                  <tr>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                    <td>text-text</td>
                  </tr>
                  
                </tbody>
              </table>
            </div>
            <div className="players_footer">
              <div className="players_footer__item">
                <p>Players: <span>17</span></p>
              </div>
              <div className="players_footer__item">
                <p>Betting: <span>12,25</span> ETH and <span>7,89</span> BTC</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home