import SideBar from '../components/Admin/Sidebar';
import MessageModal from '../components/Admin/MessageModal';
import Table from '../components/Admin/Table';
import { useState, useEffect } from 'react';

const Messages = () => {

    const [showWindow, $showWindow] = useState(false);
    const [dayWeek, $dayWeek] = useState(new Date().toLocaleDateString('en-EN', { weekday: 'long' }));
    const [tableData, $tableData] = useState([]);

    const dataTable = {
        head: ['Sender', 'Date', 'Text', 'Status'],
        body: tableData
    }

    function toggleShowWindow() {
        $showWindow(!showWindow);
    }
    function toggleActiveLi(e) {
        let active = document.querySelector('.active');
        if (active) active.classList.remove('active');
        $dayWeek(e.target.textContent)
    }
    async function updateMessage() {
        const request = await fetch('/api/find/messages', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ currentDay: dayWeek })
        })
    
        const response = await request.json();

        if (response.ok) {
            $tableData(response.messages)
        } else {
            console.log(response.message);
        }
    }

    useEffect(() => {
        async function getData() {
            const lis = document.querySelectorAll('.daysWeek li');
            for (let li of lis) {
                if (li.textContent === dayWeek) {
                    li.classList.add('active');
                    
                    await updateMessage();

                    break;
                }
            }
        }

        getData();

    }, [dayWeek]);

    useEffect(() => {
        async function getData() {
            await updateMessage();
        }
        getData();
    }, [])

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_header">
                    <div className="admin_content_header_item">
                        <ul className='daysWeek'>
                            <li onClick={toggleActiveLi}>Monday</li>
                            <li onClick={toggleActiveLi}>Tuesday</li>
                            <li onClick={toggleActiveLi}>Wednesday</li>
                            <li onClick={toggleActiveLi}>Thursday</li>
                            <li onClick={toggleActiveLi}>Friday</li>
                            <li onClick={toggleActiveLi}>Saturday</li>
                            <li onClick={toggleActiveLi}>Sunday</li>
                        </ul>
                    </div>
                    <div className="admin_content_header_item">
                        <button className="mini-button" onClick={ toggleShowWindow }>Create message</button>
                    </div>
                </div>
                <div className="admin_content_block">
                    <div className="admin_content_block_body scrollStyle">
                        <Table data={ dataTable } />
                    </div>
                </div>
            </div>
            <MessageModal dayWeek={ dayWeek } updateTable={ updateMessage } show={ showWindow } closeWindow={ toggleShowWindow } />
        </div>
    )
}

export default Messages