import ArrowDown from '../../assets/arrow-down.png';
import Add from '../../assets/sidebar/add.png';
import Message from '../../assets/sidebar/message.png';
import Users from '../../assets/sidebar/users.png';
import Settings from '../../assets/sidebar/settings.png';
import Logout from '../../assets/sidebar/logout.png';

import { NavLink } from "react-router-dom";

const Sidebar = () => {

    const logoutHendler = () => {
        localStorage.removeItem('token');
        localStorage.removeItem('userId');
        document.location = '/';
    }

    const sidebarDropMenu = (e) => {
        const dropArrow = document.querySelector('.drop-arrow')
        const sidebarDrop = document.querySelector('.admin_sidebar_header_drop')
        dropArrow.classList.toggle('drop-arrow--active')
        sidebarDrop.classList.toggle('sidebar_header_drop--active')
      }

    return (
        <div className="admin_sidebar">
            <div onClick={sidebarDropMenu} className="admin_sidebar_header">
                <span>AA</span>
                <p>Admin</p>
                <div className="drop-arrow">
                    <img src={ArrowDown} alt="" className="drop_arrow_down" />
                </div>
            </div>
            <div className="admin_sidebar_header_drop">
                <button to='/' onClick={logoutHendler} className="admin_sidebar_nav_item">
                    <img src={Logout} alt="" />
                    <p>Logout</p>
                </button>
            </div>
            <div className='admin_sidebar_nav'>
                <div className="hr"></div>
                <NavLink to='/admin/bots' className={({isActive}) => 
                "admin_sidebar_nav_item" + (isActive ? " active--sidebar" : "")}>
                    <img src={Add} alt="" />
                    <p>Bots</p>
                </NavLink>
                <NavLink to='/admin/messages' className={({isActive}) => 
                "admin_sidebar_nav_item" + (isActive ? " active--sidebar" : "")}>
                    <img src={Message} alt="" />
                    <p>Messages</p>
                </NavLink>
                <NavLink to='/admin/users' className={({isActive}) => 
                "admin_sidebar_nav_item" + (isActive ? " active--sidebar" : "")}>
                    <img src={Users} alt="" />
                    <p>Users</p>
                </NavLink>
                <div className="hr"></div>
                <NavLink to='/admin/settings' className={({isActive}) => 
                "admin_sidebar_nav_item" + (isActive ? " active--sidebar" : "")}>
                    <img src={Settings} alt="" />
                    <p>Settings</p>
                </NavLink>
            </div>
        </div>
    )
}

export default Sidebar