import SideBar from '../components/Admin/Sidebar';
import BotModal from '../components/Admin/BotModal';
import { useState, useEffect } from 'react';
import Table from '../components/Admin/Table';

const Bots = () => {

    const [showWindow, $showWindow] = useState(false);
    const [tableData, $tableData] = useState([]);

    const dataTable = {
        head: ['Login', 'Currency', 'Bet amount', 'Luck', 'Time interval', 'Actions'],
        body: tableData
    }

    function toggleShowWindow() {
        $showWindow(!showWindow);
    }
    async function updateUsers() {
        function setTextInterval(object) {
          let days = {
            0: 'Sun',
            1: 'Mon',
            2: 'Tue',
            3: 'Wed',
            4: 'Thu',
            5: 'Fri',
            6: 'Sat'
          }
    
          let str = '';
    
          let now = new Date();
          let day = now.getDay();
          let dayWeek = days[day];
    
          str += `${dayWeek}:`;
    
          if (dayWeek in object) {
            let arr = object[dayWeek];
            let nArr = arr.map(item => { return `${item.start} - ${item.end}` });
            str += nArr.join(', ')
            
          }
    
          return str;
    
        }
    
        const request = await fetch('/api/find/users', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify()
        })
    
        const response = await request.json();
        
        if (response.ok) {
            const users = response.users;
        
            let arr = [];
        
            users.forEach(user => {
                let intervals = setTextInterval(user.intevals);
                arr.push([user.login, user.currency, `${user.amountMin} - ${user.amountMax}`, user.luck, intervals, '']);
            })
        
            $tableData(arr);
    
        } else {
            console.log(response.message);
        }
    }
    
    useEffect(() => {
        async function getData() {
            await updateUsers();
        }
        getData();
        
    }, [])

    return (
        <div className='block_admin'>
            <SideBar />
            <div className="admin_content">
                <div className="admin_content_block admin_content_block--max">
                    <div className="admin_content_block_header">
                        <h3>List of Bots</h3>
                        <button className="mini-button" onClick={ toggleShowWindow }>Create Bot</button>
                    </div>
                    <div className="admin_content_block_body scrollStyle">
                        <Table data={ dataTable } />
                    </div>
                </div>
            </div>
            <BotModal show={ showWindow } closeWindow={ toggleShowWindow } updateTable={ updateUsers } />
        </div>
    )
}

export default Bots