import React, { useRef } from "react"
import { Toast } from 'primereact/toast';


const Login = () => {

  const toast = useRef(null);

  const token = localStorage.getItem('token')

  if (token) document.location.href = '/admin/bots'

  const loginHengler = async () => {
    const email = document.getElementById('emailInput')
    const password = document.getElementById('passwordInput')

    const form = {
      login: email.value,
      password: password.value
    }

    const request = await fetch('/api/auth/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(form)
    })

    const response = await request.json()

    if (response.ok) {
        if (response.token && response.userId) {
            const token = response.token
            const userId = response.userId
      
            localStorage.setItem('token', token)
            localStorage.setItem('userId', userId)
      
            document.location = '/admin/bots'
        } else {
          toast.current.show({severity:'error', summary: 'Error Message', detail: response.message, life: 5000});
        }
    } else {
      toast.current.show({severity:'error', summary: 'Error Message', detail:'Error Server', life: 5000});
    }
  }

  return (
    <div className="login">
      <div className="login_block">
        <h2 className="login_title">Log In</h2>

        <div className="login_value">

          <div className="login_item">
            <p>Login</p>
            <input id="emailInput" className="login_item_input" type="text" placeholder="press your email...." />
          </div>

          <div className="login_item">
            <p>Password</p>
            <input id="passwordInput" className="login_item_input" type="text" placeholder="press your password...." />
          </div>

        </div>

        <div className="forgot">
          <a href="/forgot-password">Forgot your password?</a>
        </div>

        <div className="btn-container--center">
          <button onClick={loginHengler} className="btn btn--center">Login</button>
        </div>

      </div>
      <Toast ref={ toast } />
    </div>
  )
}

export default Login